function getMoviesFromMongoDB(type, context) {
	var url = "https://gdjactscaf.execute-api.us-east-1.amazonaws.com/Prod/retrieveMovies?";
	const title = context.title;
	const text = context.text;
	const genre = context.genre;
	const start = context.start;
	const end = context.end;

	if (type === "find_one") {
		url = url + "type=" + type + "&title=" + title;
	}
	else if (type === "advanced") {
		url = url + "type=" + type + "&text=" + text;
	}
	else {
		url = url + "type=" + type + "&genre=" + genre + "&start=" + start + "&end=" + end;
	}


	return fetch(url)
	      .then(res => res.json())
	      .then(
	        (result) => {
	        	// console.log(result);
	          return result;
	        },
	        // Note: it's important to handle errors here
	        // instead of a catch() block so that we don't swallow
	        // exceptions from actual bugs in components.
	        (error) => {
	          alert(error);
	        }
	       );
}

function getImages(imdbId) {
	const testUrl = "https://api.themoviedb.org/3/find/" + imdbId + "?api_key=5256f55becfcbae4f52f802e2173adcb&language=en-US&external_source=imdb_id";
	return fetch(testUrl)
	.then(res => res.json())
	.then(result => {console.log(result); return result.movie_results;})
	.then((result) => 
		{return [result[0].poster_path, result[0].backdrop_path]})
	.catch(
		error => {//do nothing
	});
}

